}

.chat {
  position: relative;
  z-index: 999;
}
.chat::before {
  position: fixed;
  content: "";
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.9);
  left: 0;
  top: 0;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s ease opacity;
}
.chat__ctn {
  position: fixed;
  right: -100%;
  top: 0;
  height: 100vh;
  background-color: rgba(26, 74, 140, 0.9);
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
  gap: 30px;
  padding: 40px 35px 140px 40px;
  transition: 1s all ease;
  width: 330px;
  max-width: 330px;
}
.chat__item {
  font-size: 1.6rem;
  color: #fff;
}
.chat__img {
  width: 50px;
  height: 50px;
}
.chat__item {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
.chat__item p {
  transition: 0.5s ease all;
}
.chat__item:hover p {
  margin-right: 20px;
}
.chat__img, .chat__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.5s ease all;
  border-radius: 100px;
  box-shadow: 0px 4px 11px rgba(0, 0, 0, 0.2), 0px 1px 5px rgba(0, 0, 0, 0.25);
  background-color: #fff;
  border: 2px solid #1A4A8C;
  cursor: pointer;
}
.chat__btn {
  position: fixed;
  right: 30px;
  bottom: 40px;
  width: 60px;
  height: 60px;
}
.chat__btn img {
  width: 27px;
  transition: 0.5s ease all;
  position: absolute;
  transform: rotate(0deg);
}
.chat__btn img:last-child {
  width: 30px;
  opacity: 0;
}
.chat.active::before {
  pointer-events: all;
  opacity: 1;
}
.chat.active .chat__btn img:first-child {
  opacity: 0;
  transform: rotate(360deg);
}
.chat.active .chat__btn img:last-child {
  opacity: 1;
  transform: rotate(360deg);
}
.chat.active .chat__ctn {
  right: 0;
}
.chat-animation {
  width: 50px;
  height: 50px;
  display: inline-block;
  padding: 0px;
  text-align: left;
}
.chat-animation__ctn {
  position: fixed;
  transition: 0.3s all ease;
  opacity: 1;
  right: 58px;
  bottom: 65px;
  z-index: -1;
}
.chat-animation__ctn.active {
  opacity: 1;
  transition: 0.5s all ease;
}
.chat-animation span {
  position: absolute;
  display: inline-block;
  width: 95px;
  height: 95px;
  border-radius: 100%;
  background: #00BDD6;
  animation: chat 1.5s linear infinite;
}
.chat-animation span:last-child {
  animation-delay: -0.9s;
}

@keyframes chat {
  0% {
    transform: scale(0, 0);
    opacity: 0.8;
  }
  100% {
    transform: scale(1, 1);
    opacity: 0;
  }
}
@media (max-width: 576px) {
  .chat__ctn {
    width: 100%;
    max-width: 100%;
    padding: 40px 25px 190px 40px;
  }
  .chat__btn {
    width: 50px;
    height: 50px;
    right: 20px;
    bottom: 100px;
  }
  .chat__img {
    width: 40px;
    height: 40px;
  }
  .chat.active::before {
    display: none;
  }
  .chat-animation {
    width: 50px;
    height: 50px;
  }
  .chat-animation__ctn {
    right: 28px;
    bottom: 106px;
  }
  .chat-animation span {
    width: 65px;
    height: 65px;
  }
}
