  opacity: 0;
  transform: translateY(40px);
  transition: 1s ease;
  transition-property: opacity, transform;
}
.aos-custom-fade-left {
  transform: translateX(-100px);
  transition: 1s ease;
  transition-property: opacity, transform;
}
.aos-custom-fade-right {
  transform: translateX(100px);
  transition: 1s ease;
  transition-property: opacity, transform;
}
.aos-custom-scale {
  transform: scaleX(0);
  transition: 1s ease transform;
}
.aos-custom-scaleX, .aos-custom-scaleXreverse {
  transform: scale(0);
  position: relative;
  transition: 1s ease all;
}
.aos-custom-scaleX {
  left: -150px;
}
.aos-custom-scaleXreverse {
  right: -150px;
}
.aos-custom-rotate {
  transform: scale(0.5);
  opacity: 0;
  transition: 1s ease all;
}
.aos-custom.aos-animate {
  opacity: 1;
}

.aos-activate.aos-animate .aos-custom-scale {
  transform: scale(100%);
}
.aos-activate.aos-animate .aos-custom-scaleX {
  transform: scale(1);
  left: 0;
}
.aos-activate.aos-animate .aos-custom-scaleXreverse {
  transform: scale(1);
  right: 0;
}
.aos-activate.aos-animate .aos-custom-fade-left,
.aos-activate.aos-animate .aos-custom-fade-right {
  transform: translateX(0);
}
.aos-activate.aos-animate .aos-custom-fade-up {
  transform: translateY(0);
}
.aos-activate.aos-animate .aos-custom-rotate {
  transform: scale(1);
  opacity: 1;
}
.aos-activate.aos-animate .aos-custom-fade-up,
.aos-activate.aos-animate .aos-custom-fade-left,
.aos-activate.aos-animate .aos-custom-fade-right {
  opacity: 1;
