.toggle,
.toggleTurisemana,
.toggle-mobile {
  overflow: hidden;
  position: relative;
  margin-top: 20px;
}
.toggle.removeOverFlow,
.toggleTurisemana.removeOverFlow,
.toggle-mobile.removeOverFlow {
  overflow: visible;
}
.toggle__show,
.toggleTurisemana__show,
.toggle-mobile__show {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 20px;
}
.toggle__show::after,
.toggleTurisemana__show::after,
.toggle-mobile__show::after {
  position: relative;
  content: "";
  width: 20px;
  height: 15px;
  background: url("../assets/icons/icon-down-blue.svg") no-repeat;
  background-position: 50%;
  transition: 0.3s ease transform;
}
.toggle__show.active::after,
.toggleTurisemana__show.active::after,
.toggle-mobile__show.active::after {
  transform: rotateX(180deg);
}
.toggle__show.disabled,
.toggleTurisemana__show.disabled,
.toggle-mobile__show.disabled {
  pointer-events: none;
}
.toggle__hidden,
.toggleTurisemana__hidden,
.toggle-mobile__hidden {
  height: 0;
  transition: 1s ease;
  transition-property: height, opacity;
}
.toggle__hidden .input-custom__checkbox--blue,
.toggleTurisemana__hidden .input-custom__checkbox--blue,
.toggle-mobile__hidden .input-custom__checkbox--blue {
  justify-content: flex-start;
  margin-bottom: 20px;
}
.toggle__hidden .input-custom__select .options,
.toggleTurisemana__hidden .input-custom__select .options,
.toggle-mobile__hidden .input-custom__select .options {
  transition-duration: 0.1s;
  margin-bottom: 20px;
}
.toggle__hidden .input-custom__select,
.toggleTurisemana__hidden .input-custom__select,
.toggle-mobile__hidden .input-custom__select {
  margin-bottom: 20px;
}

