.swiperTurisemana .swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}
.swiperTurisemana .swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.swiperTurisemana .swiper-button-next,
.swiperTurisemana .swiper-button-prev {
  width: 3rem;
  height: 3rem;
  border-radius: 5px;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.3);
  background-color: #fff;
  transform: translateY(50%);
}
.swiperTurisemana .swiper-button-next::before, .swiperTurisemana .swiper-button-next::after,
.swiperTurisemana .swiper-button-prev::before,
.swiperTurisemana .swiper-button-prev::after {
  content: "";
  display: block;
  width: 1rem;
  height: 2px;
  background-color: #1A4A8C;
  position: absolute;
  left: 50%;
}
.swiperTurisemana .swiper-button-next::before,
.swiperTurisemana .swiper-button-prev::before {
  top: 37%;
  transform: translateX(-50%) rotate(45deg);
}
.swiperTurisemana .swiper-button-next::after,
.swiperTurisemana .swiper-button-prev::after {
  top: 58%;
  transform: translateX(-50%) rotate(-45deg);
}
.swiperTurisemana .swiper-button-prev {
  transform: translateY(50%) scaleX(-1);
}
.swiperTurisemana .swiper-button-next {
