.tab-general {
  position: relative;
  overflow: hidden;
  padding-bottom: 10px;
}
.tab-general-ctn {
  position: absolute;
  opacity: 0;
  pointer-events: none;
  left: 0;
  top: 0;
}
.tab-general-ctn.active {
  pointer-events: all;
  animation: opacity-to-absolute 1s ease forwards;
}
.tab-general-btn {
  cursor: pointer;
}

.hidden-options-tab {
  transition: 1s ease all;
}
.hidden-options-tab.width.hidden {
  animation: opacity 0.25s ease forwards 1;
  width: 0 !important;
  min-width: 0 !important;
}
.hidden-options-tab.height {
  transition-duration: 0.5s;
  opacity: 1;
  overflow: hidden;
}
.hidden-options-tab.height.hidden {
  opacity: 0;
  height: 0;
}

@keyframes opacity-to-absolute {
  from {
    position: relative;
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  to {
    opacity: 1;
    position: relative;
  }
}
@keyframes opacity {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
form fieldset {
